import styles from './Main.module.css'

export type Pizza = {
    id: number;
    nome: string;
    descricao: string;
    preco: number;
    imagem: string;
};

type props = {
    pizzas: Pizza[];
}

export default function Main({ pizzas }: props) {
    return (
        <main>
            <section>
                <div className={styles.Heading}>
                    <h1>Seu Carrinho</h1>
                </div>
                <div className={styles.Container}>
                    <div className={styles.Informacoes}>
                        {Array.isArray(pizzas) && pizzas.map((pizza: Pizza) => (
                            <div key={pizza.id} className={styles.Pizza}>
                                <h1>{pizza.nome}</h1>
                            </div>
                        ))}

                    </div>
                </div>
            </section>

        </main>

    );
}